import "./styles.css";

var app = document.getElementById("app");

app.innerHTML = `
<input id="radius" value="0" type="number"></input>
<label for="radius">Radius</label><br>
<input id="diameter" value="0" type="number"></input>
<label for="diameter">Diameter</label><br>
<input id="circumference" value="0" type="number"></input>
<label for="circumference">Circumference</label><br>
<input id="area" value="0" type="number"></input>
<label for="area">Area</label>
`;

document.getElementById("radius").addEventListener("input", (evt) => {
  document.getElementById("diameter").value =
    parseFloat(document.getElementById("radius").value, 10) * 2;
  document.getElementById("circumference").value =
    parseFloat(document.getElementById("radius").value, 10) * Math.PI * 2;
  document.getElementById("area").value =
    parseFloat(document.getElementById("radius").value, 10) *
    parseFloat(document.getElementById("radius").value, 10) *
    Math.PI;
});

document.getElementById("diameter").addEventListener("input", (evt) => {
  document.getElementById("radius").value =
    parseFloat(document.getElementById("diameter").value, 10) / 2;
  document.getElementById("circumference").value =
    parseFloat(document.getElementById("diameter").value, 10) * Math.PI;
  document.getElementById("area").value =
    (parseFloat(document.getElementById("diameter").value, 10) / 2) *
    (parseFloat(document.getElementById("diameter").value, 10) / 2) *
    Math.PI;
});

document.getElementById("circumference").addEventListener("input", (evt) => {
  document.getElementById("radius").value =
    parseFloat(document.getElementById("circumference").value, 10) /
    2 /
    Math.PI;
  document.getElementById("diameter").value =
    parseFloat(document.getElementById("circumference").value, 10) / Math.PI;
  document.getElementById("area").value =
    (parseFloat(document.getElementById("circumference").value, 10) *
      parseFloat(document.getElementById("circumference").value, 10)) /
    (4 * Math.PI);
});

document.getElementById("area").addEventListener("input", (evt) => {
  document.getElementById("radius").value = Math.sqrt(
    document.getElementById("area").value / Math.PI
  );
  document.getElementById("diameter").value =
    Math.sqrt(parseFloat(document.getElementById("area").value, 10) / Math.PI) *
    2;
  document.getElementById("circumference").value =
    2 *
    Math.sqrt(parseFloat(Math.PI * document.getElementById("area").value, 10));
});
